import React, { useEffect, useState, useRef } from 'react';
import { Link } from 'react-router-dom';
import '../css/Header.css';
import CollapseMenu from './CollapseMenu';

const Header = () => {

    const [widthScreen, setWidthScreen] = useState('');
    const [mobilOn, setMobilOn] = useState(true);

    const buttonMenu = useRef(null)

    const mobile = () => {
        if (
            !(navigator.userAgent.match(/Android/i)) ||
            !(navigator.userAgent.match(/webOS/i)) ||
            !(navigator.userAgent.match(/iPhone/i)) ||
            !(navigator.userAgent.match(/iPod/i)) ||
            !(navigator.userAgent.match(/iPad/i)) ||
            !(navigator.userAgent.match(/BlackBerry/i))
        ) {
            setMobilOn(!mobilOn)
        }
    };

    useEffect(() => {
        mobile()
    }, []);

    return (
        <div className="header-top bg-primary">
            <nav className="navbar navbar-expand-lg navbar-dark">
                <Link className="navbar-brand text-white font-weight-bold" to="/">Tramitero.com</Link>
                <button className="navbar-toggler" ref={buttonMenu} type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse justify-content-center" id="navbarNavDropdown">
                    {mobilOn ?
                        <CollapseMenu buttonMenu={buttonMenu} />
                        :
                        <ul className="navbar-nav w-100">
                            <li className="nav-item">
                                <Link className="nav-link dropdown-toggle text-white" aria-controls="Anses" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    Anses
                                </Link>
                                <div className="dropdown-content" aria-labelledby="Anses">
                                    <Link className="dropdown-item" to="/Anses/prestamos-jubilados-y-pensionados">Prestamos Jubilados y Pensionados</Link>
                                    <Link className="dropdown-item" to="/Anses/prestamos-auh-y-suaf">Prestamos AUH y SUAF</Link>
                                    <Link className="dropdown-item" to="/Anses/tarjeta-alimentaria">Tarjeta Alimentaria</Link>
                                    <Link className="dropdown-item" to="/Anses/fondo-de-desempleo">Fondo de Desempleo</Link>
                                    <Link className="dropdown-item" to="/Anses/asignacion-familiar-por-hijo">Asignaci√≥n Familiar por Hijo</Link>
                                    <Link className="dropdown-item" to="/Anses/asignacion-universal-por-hijo">Asignaci√≥n Universal por Hijo</Link>
                                </div>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link dropdown-toggle text-white" aria-controls="Afip" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    Afip
                            </Link>
                                <div className="dropdown-content" aria-labelledby="Afip">
                                    <Link className="dropdown-item" to="/Afip/clave-fiscal">Clave Fiscal</Link>
                                    <Link className="dropdown-item" to="/Afip/formulario-ceta">Formulario CETA</Link>
                                    <Link className="dropdown-item" to="/Afip/factura-electronica">Factura Electr√≥nica</Link>
                                    <Link className="dropdown-item" to="/Afip-inscripci√≥n-monotributo">Incripci√≥n al Monotributo</Link>
                                    <Link className="dropdown-item" to="/Afip/turnos-afip">Turnos Afip</Link>
                                    <Link className="dropdown-item" to="/Afip/generar-vep">Generar VEP</Link>
                                </div>
                            </li>
                            <li className="nav-item" onClick={e => e.target.addEventListener('click', e.preventDefault())}>
                                <Link className="nav-link dropdown-toggle text-white" aria-controls="Renaper" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    Renaper
                            </Link>
                                <div className="dropdown-content" aria-labelledby="Renaper">
                                    <Link className="dropdown-item" to="/Renaper/tramite-dni">Tr√°mite DNI</Link>
                                    <Link className="dropdown-item" to="/Renaper/tramite-pasaporte">Tr√°mite Pasaporte</Link>
                                    <Link className="dropdown-item" to="/Renaper/certificado-nacimiento">Cert. Nacimiento</Link>
                                    <Link className="dropdown-item" to="/Renaper/certificado-casamiento">Cert. Casamiento</Link>
                                    <Link className="dropdown-item" to="/Renaper/certificado-de-defuncion">Cert. de Defunci√≥n</Link>

                                </div>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link dropdown-toggle text-white" aria-controls="Automotor" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    Automotor
                            </Link>
                                <div className="dropdown-content" aria-labelledby="Automotor">
                                    <Link className="dropdown-item" to="/Automotor/turno-registro-automotor">Turno Registro Automotor</Link>
                                    <Link className="dropdown-item" to="/Automotor/transferencia-automotor">Transferencia Automotor</Link>
                                    <Link className="dropdown-item" to="/Automotor/patentamiento-automotor">Patentamiento Automotor</Link>
                                    <Link className="dropdown-item" to="/Automotor/titulo-automotor">Titulo Automotor</Link>
                                    <Link className="dropdown-item" to="/Automotor/tramite-cedula-azul">Tr√°mite Cedula Azul</Link>
                                </div>
                            </li>
                        </ul>}
                    <form className="form-inline my-2 my-lg-0">
                        <input className="form-control" type="search" placeholder="Buscar" aria-label="Buscar" />
                    </form>
                </div>
            </nav>
        </div>
    )
};

export default Header;